export function loadContacts() {
    const contentDiv = document.querySelector("#content");

    const contactHeading = document.createElement("h1");
    contactHeading.classList.add("main-heading");
    contactHeading.textContent = "Contact Us";

    const contactsDiv = document.createElement("div");
    contactsDiv.classList.add("contacts");

    const contact1 = document.createElement("div");
    contact1.classList.add("container");
    
    const contact1Name = document.createElement("h2");
    contact1Name.classList.add("item-title");
    contact1Name.textContent = "Marco Rossi";

    const contact1Description = document.createElement("p");
    contact1Description.classList.add("item-description");
    contact1Description.textContent = "Chef";

    const contact1Phone = document.createElement("p");
    contact1Phone.classList.add("item-phone");
    contact1Phone.textContent = "555-555-5554";

    const contact1Mail = document.createElement("p");
    contact1Mail.classList.add("item-mail");
    contact1Mail.textContent = "totallyRealEmail@notFake.com";

    const contact2 = document.createElement("div");
    contact2.classList.add("container");
    
    const contact2Name = document.createElement("h2");
    contact2Name.classList.add("item-title");
    contact2Name.textContent = "Alex Rosetti";

    const contact2Description = document.createElement("p");
    contact2Description.classList.add("item-description");
    contact2Description.textContent = "Waitress";

    const contact2Phone = document.createElement("p");
    contact2Phone.classList.add("item-phone");
    contact2Phone.textContent = "555-555-5555";

    const contact2Mail = document.createElement("p");
    contact2Mail.classList.add("item-mail");
    contact2Mail.textContent = "JustARealEmail@notFake.com";

    const contact3 = document.createElement("div");
    contact3.classList.add("container");
    
    const contact3Name = document.createElement("h2");
    contact3Name.classList.add("item-title");
    contact3Name.textContent = "Luca Albino";

    const contact3Description = document.createElement("p");
    contact3Description.classList.add("item-description");
    contact3Description.textContent = "Manager";

    const contact3Phone = document.createElement("p");
    contact3Phone.classList.add("item-phone");
    contact3Phone.textContent = "555-555-5556";

    const contact3Mail = document.createElement("p");
    contact3Mail.classList.add("item-mail");
    contact3Mail.textContent = "perfectlyRealEmail@notFake.com";

    contact1.append(contact1Name, contact1Description, contact1Phone, contact1Mail);
    contact2.append(contact2Name, contact2Description, contact2Phone, contact2Mail);
    contact3.append(contact3Name, contact3Description, contact3Phone, contact3Mail);

    contactsDiv.append(contact1, contact2, contact3);
    
    contentDiv.append(contactHeading, contactsDiv);
    
}